.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

/* new css */
.button {
  background: red;
  border-radius: 8px;
  width: 150px;
  height: 40px;
  color: white;
}

.input {
  margin: auto;
  position: absolute;
  text-align: center;
  width: 100%;
}

.checkboxes {
  text-align: left;
  margin-top: 20px;
}